.story .circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    -webkit-animation: animatesquare 25s linear infinite;
            animation: animatesquare 25s linear infinite;
    bottom: 30px;
    
}

.story .circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    /* animation-delay: 0s; */
}


.story .circles li:nth-child(2){
